        z-index: 12;
        position: fixed;
        top: 0;
        right: 0;
        pointer-events: none;
        opacity: 0;
        color: $color-primary;
        transform: translateX($sidebar-width);
        // transition: opacity $time-s ease-in-out;

        &.behind {
            opacity: 0 !important;
        }

        &.active {
            opacity: 1;
            transition: opacity $time-s ease-in-out;
        }

        &-text {
            line-height: 150%;
            font-size: $font-size-m;
            margin-block-end: 0;
        }

        &-item {
            background: $color-white;
            border-radius: 4px;
            position: absolute;
            width: 300px;
            height: auto;
            opacity: .9;
            padding: 15px;
            transform: translate(-50%, -50%);
            display: flex;
            flex-direction: column;
        }

        &__line {
            stroke: $color-primary;
            stroke-width: 1;
            stroke-dasharray: 3,3;
        }

        circle.anchor {
            fill: $color-primary;
        }

        .popup-title {
            margin-top: 5px;
            font-weight: 600;
            margin-bottom: 5px;
            width: 270px;
            font-size: $font-size-m !important;
            line-height: $line-height-m;
            font-size: $font-size-ml;
        }

        .popup-text {
            line-height: 150%;
            margin-top: 0;
            font-size: $font-size-m !important;
            margin-block-end: 0;
        }

        .popup-subtitle {
            margin-top: 0;
            font-weight: 600;
            line-height: $line-height-m;
            font-size: $font-size-s;
            text-transform: uppercase;
            letter-spacing: $letter-spacing-l;
            opacity: 0.5;
        }

        .popup-sequence {
            font-size: $font-size-m;
            margin-bottom: 15px;
        }

        .popup-description {
            margin-top: 0px;
            font-size: $font-size-m;
            margin-bottom: 15px;
            color: $color-primary-50;
        }


    }
</style>


